<template>
  <v-row class="no-gutters">
    <v-col cols="2 ">
      <NavigationBarLeft/>
    </v-col>
    <v-col cols="max">
      <ProfileBar/>
      <MemberInfo/>
    </v-col>
  </v-row>
</template>

<script>
import NavigationBarLeft from "../../components/profile/navigation/NavigationBarLeft";
import ProfileBar from "../../components/profile/navigation/ProfileBar";
import MemberInfo from "../../components/profile/MemberInfo";
import VueCookies from "vue-cookies";
import router from "../../router";
export default {
  name: "ProfileMembers",
  components: {MemberInfo, NavigationBarLeft, ProfileBar},
  methods:{
    checkToken(){
      if(VueCookies.get("token") === '' || VueCookies.get("token") == null){
        router.push({path: '/admin'})
      }
    }
  },
  beforeMount() {
    this.checkToken()
  }
}
</script>

